import React from 'react';
import { Link } from 'react-router-dom';
import '../styles/histories.css'
import HomeBackground from './HomeBackground';
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'

const Corazon = () => {
    const MySwal = withReactContent(Swal)
    const handleReferenceAlert = () => {
        MySwal.fire({
            html: `
                    <div class="poe--container">
                    <h1>El cuervo</h1> 
                    <p>Edgar Allan P.</p>
                    <p>
                        Cierta noche aciaga, cuando, con la mente cansada,
                        meditaba sobre varios libracos de sabiduría ancestral
                        y asentía, adormecido, de pronto se oyó un rasguido,
                        como si alguien muy suavemente llamara a mi portal.
                        "Es un visitante -me dije-, que está llamando al portal;
                        sólo eso y nada más."
                    </p>
                    <p>
                        ¡Ah, recuerdo tan claramente aquel desolado diciembre!
                        Cada chispa resplandeciente dejaba un rastro espectral.
                        Yo esperaba ansioso el alba, pues no había hallado calma
                        en mis libros, ni consuelo a la perdida abismal
                        de aquella a quien los ángeles Leonor podrán llamar
                        y aquí nadie nombrará.
                    </p>
                    <p>
                        Cada crujido de las cortinas purpúreas y cetrinas
                        me embargaba de dañinas dudas y mi sobresalto era tal
                        que, para calmar mi angustia repetí con voz mustia:
                        "No es sino un visitante que ha llegado a mi portal;
                        un tardío visitante esperando en mi portal.
                        Sólo eso y nada más".
                    </p>
                    <p>
                        Mas de pronto me animé y sin vacilación hablé:
                        "Caballero -dije-, o señora, me tendréis que disculpar
                        pues estaba adormecido cuando oí vuestro rasguido
                        y tan suave había sido vuestro golpe en mi portal
                        que dudé de haberlo oído...", y abrí de golpe el portal:
                        sólo sombras, nada más.
                    </p>
                    <p>
                        La noche miré de lleno, de temor y dudas pleno,
                        y soñé sueños que nadie osó soñar jamás;
                        pero en este silencio atroz, superior a toda voz,
                        sólo se oyó la palabra "Leonor", que yo me atreví a susurrar...
                        sí, susurré la palabra "Leonor" y un eco volvióla a nombrar.
                        Sólo eso y nada más.
                    </p>
                    <p>
                        Aunque mi alma ardía por dentro regresé a mis aposentos
                        pero pronto aquel rasguido se escuchó más pertinaz.
                        "Esta vez quien sea que llama ha llamado a mi ventana;
                        veré pues de qué se trata, que misterio habrá detrás.
                        Si mi corazón se aplaca lo podré desentrañar.
                        ¡Es el viento y nada más!".
                    </p>
                    <p>
                        Mas cuando abrí la persiana se coló por la ventana,
                        agitando el plumaje, un cuervo muy solemne y ancestral.
                        Sin cumplido o miramiento, sin detenerse un momento,
                        con aire envarado y grave fue a posarse en mi portal, 
                        en un pálido busto de Palas que hay encima del umbral;
                        fue, posose y nada más.
                    </p>
                    <p>
                        Esta negra y torva ave tocó, con su aire grave,
                        en sonriente extrañeza mi gris solemnidad.
                        "Ese penacho rapado -le dije-, no te impide ser
                        osado, viejo cuervo desterrado de la negrura abisal;
                        ¿cuál es tu tétrico nombre en el abismo infernal?"
                        Dijo el cuervo: "Nunca más".
                    </p>
                    <p>
                        Que una ave zarrapastrosa tuviera esa voz virtuosa
                        sorprendióme aunque el sentido fuera tan poco cabal,
                        pues acordaréis conmigo que pocos habrán tenido
                        ocasión de ver posado tal pájaro en su portal.
                        Ni ave ni bestia alguna en la estatua del portal
                        que se llamara "Nunca más".
                    </p>
                    <p>
                        Mas el cuervo, altivo, adusto, no pronunció desde el busto,
                        como si en ello le fuera el alma, ni una sílaba más.
                        No movió una sola pluma ni dijo palabra alguna
                        hasta que al fin musité: "Vi a otros amigos volar;
                        por la mañana él también, cual mis anhelos, volará".
                        Dijo entonces: "Nunca más".
                    </p>
                    <p>
                        Esta certera respuesta dejó mi alma traspuesta;
                        "Sin duda - dije-, repite lo que ha podido acopiar
                        del repertorio olvidado de algún amo desgraciado
                        que en su caída redujo sus canciones a un refrán:
                        "Nunca, nunca más".
                    </p>
                    <p>
                        Como el cuervo aún convertía en sonrisa mi porfía
                        planté una silla mullida frente al ave y el portal;
                        y hundido en el terciopelo me afané con recelo
                        en descubrir que quería la funesta ave ancestral
                        al repetir: "Nunca más".
                    </p>
                    <p>
                        Esto, sentado, pensaba, aunque sin decir palabra
                        al ave que ahora quemaba mi pecho con su mirar;
                        eso y más cosas pensaba, con la cabeza apoyada
                        sobre el cojín purpúreo que el candil hacía brillar.
                        ¡Sobre aquel cojín purpúreo que ella gustaba de usar,
                        y ya no usará nunca más!
                    </p>
                    <p>
                        Luego el aire se hizo denso, como si ardiera un incienso
                        mecido por serafines de leve andar musical.
                        "¡Miserable! -me dije-. ¡Tu Dios estos ángeles dirige
                        hacia ti con el filtro que a Leonor te hará olvidar!
                        ¡Bebe, bebe el dulce filtro, y a Leonor olvidarás!".
                        Dijo el cuervo: "Nunca más".
                    </p>
                    <p>
                        "¡Profeta! -grité-, ser malvado, profeta eres, diablo alado!
                        ¿Del Tentador enviado o acaso una tempestad
                        trajo tu torvo plumaje hasta este yermo paraje,
                        a esta morada espectral? ¡Mas te imploro, dime ya,
                        dime, te imploro, si existe algún bálsamo en Galaad!"
                        Dijo el cuervo: "Nunca más".
                    </p>
                    <p>
                        "¡Profeta! -grité-, ser malvado, profeta eres, diablo alado!
                        Por el Dios que veneramos, por el manto celestial,
                        dile a este desventurado si en el Edén lejano
                        a Leonor, ahora entre ángeles, un día podré abrazar".
                        Dijo el cuervo: "¡Nunca más!".
                    </p>
                    <p>
                        "¡Diablo alado, no hables más!", dije, dando un paso atrás;
                        ¡Que la tromba te devuelva a la negrura abisal!
                        ¡Ni rastro de tu plumaje en recuerdo de tu ultraje
                        quiero en mi portal! ¡Deja en paz mi soledad!
                        ¡Quita el pico de mi pecho y tu sombra del portal!"
                        Dijo el cuervo: "Nunca más".
                    </p>
                    <p>
                        Y el impávido cuervo osado aun sigue, sigue posado,
                        en el pálido busto de Palas que hay encima del portal;
                        y su mirada aguileña es la de un demonio que sueña,
                        cuya sombra el candil en el suelo proyecta fantasmal;
                        y mi alma, de esa sombra que allí flota fantasmal,
                        no se alzará...¡nunca más!.
                    </p>
                    </div>
                   `,
            width: '600px',
            confirmButtonText: 'Volver',
            background: '#000',
            color: '#ffffffbf'
          })
    }
    return (
        <div className='history--container'>
            <HomeBackground/>
            <div className='corazon'>
                <div className='history--text__container'>
                    <h1>Corazon de cera</h1>
                    <button 
                        onClick={() => handleReferenceAlert()}
                        className="reference--button"
                    >
                            El cuervo de Edgar A. Poe
                    </button>
                    <p>
                        De cuanto ocurrió después de que hube tomado la pluma tras poner mi firma, no se si es sueño pesadilla o la dura realidad que nos golpea en la cara, como el sol o el viento de todos los días,  sin embargo, yo había puesto mi alma en manos de las potencias de la noche,  de la muerte  y la oscuridad eterna, de la demencia demoníaca y violenta de mi cuerpo ardiente y febril de sexo…puso suavemente su cigarrillo en mi boca y con su otra mano me entrego la pluma maldita del Cuervo Rojo.
                        <br />
                        <br />
                        Los colmillos sanguinolentos de una luna creciente de septiembre, apenas proyectaban una débil penumbra. En un oscuro recodo del camino que me conducía a mi destino me encontré, casualmente, con una difusa figura que se hallaba  detenida ahí, muy cerca. Me preguntó con una atractiva voz de mujer, si podría darle fuego ya que se había agotado su mechero. Entonces, al encender su cigarillo, pude notar un brillo profundo y abismal en sus ojos negros y su cabello liso azabache que caía sueltamente sobre sus hombros de piel alabastro moreno cobrizo, cubiertos con un chal de seda escarlata. (Supe que este pequeño gesto me arrojaría a un viaje sin retorno más allá de la muerte) en aquel momento me extendió su mano afilada y helada y con un aliento avinado me dijo simplemente –soy Lilith, siempre lamentarás la noche maldita en que me has conocido. Dio una fumada a su cigarrillo que iluminó su rostro mientras me miraba, o mejor, me atravesaba con su mirada, viendo directamente como un animal salvaje mi corazón de cera que comenzó a derretirse lentamente. Para disimular mi terrible afección, me apresure a decirle mi nombre –me llamo Davier Coock –dije timidamente– soy dibujante y escritor, pero por ahora solo me intereso en el esoterismo, aunque  debo reconocerlo, poseo poca experiencia práctica… Un destello diamantino de su sonrisa helo mis miembros, impidiendo continuar mi docta presentación –yo, por el contrario posee una gran experiencia práctica en esos asuntos –dijo Lilith como entradilla para sacarme de carrera, agregando sin dilación– yo tengo Hemolimia, una extraña condición que me obliga a alimentarse cotidianamente de sangre –tosió ligeramente antes de continuar, dándome tiempo de sopesar cada una de sus palabras– Es difícil conseguir buena sangre en estos tiempos, por eso me dedico a cuidar algunas vacas de las que me aliento en algunas ocasiones, sin embargo, debo confesarle señor Davier, que de tiempo en tiempo, es decir, cada Luna llena, requiero algo de sangre humana para impedir algunos quebrantos de salud, secuela  que me provoca el consumo de sangre bovina. 
                        <br />
                        <br />
                        –Disculpe usted –dije yo– me cuesta creer que me encuentro ante una vampiresa de novela crepuscular. Ella rió conmigo, aunque algo me decía que no había que llevar más allá la broma, entonces le pregunté –¿qué tiene que ver tu condición enfermedad, locura o lo que sea, con el esoterismo?.
                        <br />
                        <br />
                        Fue entonces que conocí su lado terrible, porque se abalanzó a mi boca sin dejarme terminar la oración, me besó  tierna y apasionadamente, pero luego, con un colmillo afilado cortó la comisura de mi boca, mientras comenzaba a manar mi sangre ferrosa y caliente, la cual absorbió con deleite por un momento –disculpa –dijo tras una risa tímida, que hubiese podido emitir una colegiala ante un cumplido tonto de un jovenzuelo enamorado– es que no soporté la tentacion de catar tu sangre… ¿y sabes?, no está nada mal. ¿Qué tal si hacemos un trato tú y yo?, si aceptas, te daré algo que nunca olvidarás, porque debo decirte, que soy esposa de Lucifer, y mi marido puede brindarte algo extraordinario, un objeto mágico llamado la Pluma del Cuervo Rojo. Ante tamaño absurdo, que supuse se trataba de una broma, reí de buena gana, aunque, la soledad y el callado murmullo  de la noche apagó la pobre llamita de mi risa como un ventarrón helado, la mirada profunda escrutadora de Lilith me atravesó de nuevo el corazón dejándome absorto con una desagradable sensación de desasosiego. –No conozco tal pluma –agregué inmediatamente para cortar un silencio largo pesado– y si existe dudo que esté en su poder… además, ¿de qué se trata tal pluma? ¿Es acaso una nueva marca de lapicero chino?. lanzó una auténtica carcajada de bruja aterradora que heló la sangre en mis venas. –¿eres un tonto o solo te haces? –me escupió esta pregunta a la cara– ¿acaso no sabes que la pluma del cuervo rojo es la pluma con la que se firman los pactos faustianos?  su punta está hecha con el oro rojo y maldito del infierno, con ella pueden escribir hasta el fin de los tiempos, hacer contratos con almas o dibujar obras maestras que te traerán fama  fortuna eternas, a vez que no es poca cosa, solo hay tres en todo el mundo y yo puedo darte una, te la ofrezco sin dobleces, con esa pluma fantástica también puedes escribir una carta a la mujer que quieras y con solo poner tu nombre en ella ya estará en tu poder… ¿entendemos que por primera y última vez te ofresco convertirte en un dios en la tierra?, a cambio de un poco de tiempo y de tu sangre, claro… solo un poco por todo… eso si que es una ganga, querido Davier, solo un estupido podría rechazarlo. Luego pasó su lengua lentamente por mi barbilla empapada de sangre fresca y agitada.
                    </p>
                    <div className='history--links__container'>
                        <Link to="/">
                            {'<--- LTPDD'}
                        </Link>
                        <Link to="/dragon">
                            {'El DRAGON PLATEADO --->'}
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Corazon;
